<template>
  <div class="dropdown">
    <SakaiDropdown :items="items" :menuid="id" ref="dropdown">
      <template #activation>
        <SakaiButton
          @click="handleClick"
          :aria-controls="id"
          aria-haspopup="true"
          :aria-expanded="expanded ? 'true' : 'false'"
          :text="text"
          :clear="clear"
          :link="link"
          :circle="circle"
          :textHidden="textHidden"
          :primary="primary"
          :disabled="disabled"
        >
          <template #prepend>
            <slot name="prepend" />
          </template>
          <template #append>
            <slot name="append">
              <sakai-icon
                class="ms-1"
                :iconkey="expanded ? 'chevron_up' : 'chevron_down'"
              />
            </slot>
          </template>
        </SakaiButton>
      </template>
    </SakaiDropdown>
  </div>
</template>

<script>
import { v4 as uuid } from "uuid";
import SakaiIcon from "./sakai-icon.vue";
import SakaiDropdown from "./sakai-dropdown.vue";
import SakaiButton from "./sakai-button.vue";
export default {
  components: {
    SakaiIcon,
    SakaiButton,
    SakaiDropdown,
  },
  props: {
    items: {
      type: Array,
      default: () => [
        {
          id: 0,
          icon: "error",
          string: "emty",
          route: "/meetings-ui",
        },
      ],
    },
    text: {
      type: String,
      default: "Button",
    },
    clear: {
      type: Boolean,
      default: false,
    },
    circle: {
      type: Boolean,
      default: false,
    },
    link: {
      type: Boolean,
      default: false,
    },
    textHidden: {
      type: Boolean,
      default: false,
    },
    primary: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      id: "drop",
      selectedId: null,
      expanded: false,
    };
  },
  computed: {},
  methods: {
    handleClick: function () {
      this.expanded = !this.expanded;
    },
  },
  created: function () {
    this.id += uuid().substring(8, 13); //random id '-34F4'
  },
  mounted: function () {
    this.$watch("$refs.dropdown.expanded", (newValue) => {
      this.expanded = newValue;
    });
  },
};
</script>

<style>
@import "../assets/sakai-colors.css";
</style>
